<template>
  <div>

    <!-- 搜索 -->
    <router-link to="/search" tag="div">
      <van-search shape='round' background='#fff' placeholder="搜索平台精选好物" />
    </router-link>

    <!-- 轮播banner -->
    <div>
      <van-swipe :autoplay="3000" indicator-color="#21A2E6">
        <van-swipe-item v-for="(item,index) in data.carousel" :key="index">
          <router-link to="/"><img :src="item" alt="" class="swiper_img"></router-link>   <!-- 广告跳转 -->
        </van-swipe-item>
      </van-swipe>
    </div>

    <!-- 广告 热卖图 -->
    <!-- <router-link to="/" tag="div"><img src="@/assets/images/mall/mall_title@2x.png" alt="" class="img1"></router-link> -->





    <!-- 精品0元购 -->
    <div class="bk pd3">
      <h3>{{two.name}}</h3>
      <div class="item" v-for="(item,index) in two.list" :key="index" @click="$router.push({path:'/goodsDetail',query:{id:item.goods_id}})">
        <img :src="item.original_img" alt="" class="img">
        <div class="right">
          <div class="title">{{item.goods_name}}</div>
          <!-- <div class="btn_icon">券 | 满69减5</div> -->
          <div class="bottom">
            <div>
              <ul>
                <li class="li-title c3">限量{{item.store_count}}份</li>
                <li class="del c9">￥{{item.market_price}}</li>
              </ul>
            </div>
            <div>
              <ul>
                <li class="li-2">￥0.00元</li>
                <li class="c9">{{item.sales_sum}}人购买</li>
              </ul>
            </div>
          </div>
        </div>
      </div>
    </div>

    <!-- 今日爆款 -->
    <div class="bk pd3">
      <h3>{{one.name}}</h3>
      <div class="item" v-for="(item,index) in one.list" :key="index" @click="$router.push({path:'/goodsDetail',query:{id:item.goods_id}})">
        <img :src="item.original_img" alt="" class="img">
        <div class="right">
          <div class="title">{{item.goods_name}}</div>
          <div class="btn_icon">券 | 满69减5</div>
          <div class="bottom">
            <div>
              <ul>
                <li class="pricc">￥{{item.market_price}}</li>
                <li class="del">￥{{item.shop_price}}</li>
              </ul>
            </div>
            <div>
              <ul>
                <li class="li-1">已爆卖</li>
                <li class="li-2">{{item.sales_sum}}件</li>
              </ul>
            </div>
          </div>
        </div>
      </div>
    </div>


    <!-- 一级分类导航   :class="ii==index?'act':''" -->
    <div class="pd3 flex ja">
      <div class="nav-fenlei w70">
        <span v-for="(item,index) in stair" :key="index" :class="isact==index?'act':''" @click="isact=index;getyiji(item)">{{item.name}}</span>
      </div>
      <div class="r" @click="$router.push({path:'/fenlei'})">
        <img src="@/assets/images/mall/ic_classify.png" alt="">
        <span>分类</span>
      </div>
    </div>

    <!-- 二级分类 -->
    <div class="list_lei pd3">
      <div class="item" v-for="(item,index) in data.second" :key="index" @click="iserji=index;geterji(item)">
        <img :src="item.image" alt="" class="img">
        <span :class="iserji==index?'erji_act':''">{{item.name}}</span>
      </div>
      <div class="item" @click="$router.push({path:'/fenlei'})">
        <img src="@/assets/images/mall/ic_more.png" alt="" class="img">
        <span>查看更多</span>
      </div>
    </div>


    <!-- 数据列表  对应二级分类 -->
    <div class="list_data pd3">
      <div class="goods" @click="$router.push({path:'/goodsDetail',query:{id:item.goods_id}})" v-for="(item,index) in list.list" :key="index">
        <img :src="item.original_img" alt="" class="img">
        <div class="title ell1">{{item.goods_name}}</div>
        <div class="bottom">
          <div class="left">
            <span class="price">¥{{item.shop_price}}</span>
            <span class="market">¥{{item.market_price}}</span>
            <p class="count">{{item.sales_sum}}件已售</p>
          </div>
        </div>
      </div>
    </div>
    <div class="more" @click="getmore()" v-if="count>0"> + 加载更多 </div>   
    <div class="more" @click="getmore()" v-else> 没有更多了 </div>
    <img src="@/assets/images/mall/mall_cart.png" alt="" class="cart" @click="$router.push({path:'/cart'})">     
    <navbar :active=1></navbar>
  </div>
</template>

<script>
import { Swipe, SwipeItem, Search, Toast } from 'vant';
import { jmallindex, yiji } from '@/axios/getData'
import navbar from '@/components/index.vue'

export default {
  components: {
    'van-swipe': Swipe,
    'van-swipe-item': SwipeItem,
    'van-search': Search,
    Toast,
    navbar
  },
  data() {
    return {
      one:{},
      two:{},
      isact:0,        //  一级分类导航
      limit:4,        //  测试 每次返回四条商品数据
      data:{},      
      list:{},        //  商品列表数据
      page:1,
      stair_id:'',    //  一级分类id
      stair:[],       //  一级导航列表
      second_id:"",   //  二级分类id
      iserji:0,       //  当前二级导航
      count:1,        //  总数
    }
  },
  created() {
    var url = window.location.href
    if(url.search('openid=')!=-1){
      var openid = url.split('openid=')[1];
      localStorage.setItem('openid',openid)
    }
    this.getdata()
  },
  methods: {
    async getmore(){
      var that = this
      if( that.count<=0 ) return;
      var data =  JSON.parse(JSON.stringify(that.data))
      var data1 = {}
      data1.page = that.page
      data1.limit = that.limit
      data1.stair_id = that.stair_id      //  有可能用户没有点击
      data1.second_id = that.second_id
      console.log(data.stair[0].id )
      var res = await jmallindex(data1)
      that.count = res.data.goods_list.count
      that.list.list = that.list.list.concat(res.data.goods_list.list)
    },
    async getdata(){
      var that = this
      var data = {}
      data.page = that.page
      data.limit = that.limit
      var res = await jmallindex(data)
      this.data = res.data;
      this.stair = res.data.stair;  //  一级导航列表
      console.log(res.data.stair[0].id)
      this.stair_id = res.data.stair[0].id
      this.second_id = res.data.second[0].id
      this.list = res.data.goods_list
      that.count = res.data.goods_list.count
      this.one = res.data.one
      this.two = res.data.two
      this.page++
    },
    toast_() {
      Toast("该功能暂未开放")
    },
    async getyiji(item){   //  点击一级
      this.page = 1
      var data1 ={}
      data1.page = this.page 
      data1.stair_id = item.id
      data1.limit = this.limit
      var res = await jmallindex( data1 )
      console.log(res.data)
      this.data = res.data;
      this.list = res.data.goods_list
      that.count = res.data.goods_list.count
    },
    async geterji(item){   //  点击二级
      this.page = 1
      var data1 ={}
      data1.page = this.page
      data1.stair_id = this.stair[this.isact].id
      data1.second_id = item.id
      data1.limit = this.limit
      var res = await jmallindex( data1 )
      console.log(res.data);
      this.data = res.data;
      this.list = res.data.goods_list
      that.count = res.data.goods_list.count
    }
  }
}
</script>
<style lang="scss" scoped>
/*  */
.swiper_img{
  padding:0 3%;
  box-sizing: border-box;
  margin:0;
}
/* 广告 热卖好货 */
.img1{
  margin: 3.5% auto;
  width:36.57%;
  height:3.26%;
}

/* 广告 图文 */
.img_text{
  display: flex;
  justify-content: space-between;
  .size{
    width:45.33%;
    height:17.85%;
    border-radius: 7pt;
    box-shadow: 0 0 6px #ccc;
  }
  .r{
    width:45.33%;
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    .right{
      position: relative;
      padding:5pt;
      border-radius: 7pt;
      width:100%;
      height:47% !important;
      box-shadow: 0 0 6px #ccc;
      .title1{
        color:#434349;
        font-weight: 600;
        font-size: 11pt;
      }
      .title2{
        color:#9C9C9C;
        font-size: 9pt;
      }
      .posimg{
        position: absolute;
        top:0;
        z-index: -1;
      }
    }
  }
}
/* 二级分类 */
.list_lei{
  display: flex;
  flex-wrap: wrap;
  .item{
    display: flex;
    flex-direction: column;
    align-items: center;
    width: 18%;
    margin-right: 2.5%;
    padding: 10pt 0;
    .img{
      width:40pt;
      height:40pt;
      margin-bottom: 3pt;
    }
  }
  &>div:nth-child(5n){
    margin-right:0;
  }
}

/* 数据列表 */
.list_data{
  display: flex;
  justify-content: space-between;
  flex-wrap: wrap;
  .goods{
    width:47%;
    .img{
      width:120pt;
      height:125pt;
    }
    .title{
      margin:5pt 0;
      width:100%;
      font-size: 12pt;
      color:#000;
      font-weight: 600;
    } 
    .bottom{
      display: flex;
      justify-content: space-between;
      align-items: center;
      .left{
        .price{
          color:#EE241E;
          font-weight: 600;
          font-size: 11pt;
        }
        .market{
          text-decoration: line-through;
          color:#7b7b7b;
          font-size: 9pt;
        }
      }
    }  
  }
}
.goods + .goods{
  margin-bottom: 15pt;
}

/* 跳转购物车的图标 */
.cart{
  position: fixed;
  top:80%;
  right:5%;
  width:40pt;
  height:40pt;
}
/* 今日爆款==后加 */
.bk{
  h3{
    padding:10pt 0;
    text-align: center;
    font-size: 16pt;
  }
  .item{
    padding:5pt 0;
    display: flex;
    .img{
      margin-right:10pt;
      width:110pt;
      height:96pt;
    }
    .title{
      color:#333;
      font-size: 16pt;
      font-weight: 500;
    }
    .btn_icon{
      display: inline-block;
      width:50%;
      border:1pt solid #ED1C24;
      color:#ED1C24;
      padding:2pt;
      border-radius: 2pt;
      text-align: center;
    }
    .right{
      display: flex;
      flex-direction: column;
      justify-content: space-around;
      width:60%;
    }
  }
  .item:last-child{
    margin-bottom:10pt;
  }
  .bottom{
    display: flex;
    justify-content: space-between;
    padding-right:10pt;
    .pricc{
      display: flex;
      align-items: flex-end;
      color:#F12D2D;
      font-size: 13pt;
      .del{
        color:#999;
        font-size: 10pt;
        text-decoration-line: line-through;
      }
    }
    li{
      padding:2pt 5pt;
    }
    .li-1{
      text-align: center;
      background: #FFD6DB;
      color:#ED1C24;
    }
    .li-2{
      text-align: center;
      background: #ED1C24;
      color:#fff;
    }
  }
}
.li-title{
  font-weight: 500;
}
.del{
  text-decoration: line-through;
}

/* 分类导航 */
.ja{
  align-items: center;
  justify-content: space-around
}
.nav-fenlei{
  display: flex;
  &>span{
    box-sizing: border-box;
    display:block;
    margin-right:15pt; 
    padding:3pt 0;
    text-align: center;
  }
}
.w70{
  width:80%;
  // display: flex;
  // flex-wrap: nowrap;
  overflow-x: auto;
  white-space: nowrap;
  // overflow: scroll;
}
.w70::-webkit-scrollbar {
  display: none;
}
.r{
  display: flex;
  align-items: center;
  margin-left:5pt;
  border-radius: 15pt;
  padding:5pt;
  width:20%;
  color:#fff;
  font-size: 10pt;
  background: -webkit-linear-gradient(right ,#00C1E3,#00FFDE);
  &>img{
    margin-right:5pt;
    width:12pt;
    height:12pt;
  }
}
.act{
  position: relative;
  color: #15BDDB;
}
.act::after{
  position: absolute;
  content:'';
  display:block;
  bottom:0pt;
  width:100%;
  height:2pt;
  background: #15BDDB;
}
.erji_act{
  color:#15BDDB;
  font-weight: 600;
}
</style>




